import React, { createRef, useLayoutEffect, memo } from 'react';
import { UTTERANCES_THEME, UTTERANCES_SRC } from '@constants';
import { UtterancesLayout } from './Styles';

interface Props {
  repo: string;
  theme: UTTERANCES_THEME;
}

function Utterances({ repo, theme }: Props) {
  const containerRef = createRef<HTMLDivElement>();

  useLayoutEffect(() => {
    const utterances = document.createElement('script');

    const attributes = {
      src: UTTERANCES_SRC,
      repo,
      theme,
      'issue-term': 'pathname',
      label: 'Comments',
      crossOrigin: 'anonymous',
      async: 'true',
    };

    Object.entries(attributes).forEach(([key, value]) => {
      utterances.setAttribute(key, value as string);
    });

    if (containerRef.current) {
      containerRef.current.appendChild(utterances);
    }
  }, [repo]);

  return <UtterancesLayout ref={containerRef} />;
}

Utterances.displayName = 'Utterances';

export default memo(Utterances);
